<template>
  <div class="container">
    <!--标题-->
    <div class="title">
      <div></div>
      我的书架
      <span style="flex: 1"></span>
      <img src="/static/book_scan.png">
      <img src="/static/book_delete.png">
    </div>

    <!--很蠢的挡住-->
    <div style="height: 50px"></div>

    <!--选项-->
    <div class="tab">
      <div class="tab-item" :class="{tab_selected: seleted == 0}" @click="filter(0)">全部</div>
      <div class="tab-item" :class="{tab_selected: seleted == 1}" @click="filter(1)">在线</div>
      <div class="tab-item" :class="{tab_selected: seleted == 2}" @click="filter(2)">本地</div>
    </div>

    <!--书架-->
    <div class="book-shelter">

      <div class="item" v-for="item in items" v-if="seleted ==0 || seleted == item.local || item.local == 0">

        <img :src="item.icon">

        <div class="bookName">{{item.name}}</div>
        <div class="progress">{{item.progress}}</div>

      </div>

    </div>

    <!--书籍总数-->
    <div style="font-size: 15px;margin-top: 10px">- 共 {{bookNum}} 本 -</div>

  </div>
</template>

<script>
  export default {
    name: 'Read',
    data () {
      return {
        seleted: 0,
        items: [
          {icon: '/static/add_new_book.png', name: '', progress: '', local: 0},
          {icon: '/static/good_book1.png', name: '窗边的小豆豆', progress: '已读87%', local: 1},
          {icon: '/static/good_book2.png', name: '爱的教育', progress: '已读90%', local: 2},
          {icon: '/static/good_book3.png', name: '夏洛的网', progress: '已读55%', local: 2},
          {icon: '/static/good_book4.png', name: '雾都孤儿', progress: '已读97%', local: 2},
          {icon: '/static/good_book5.png', name: '解忧杂货店', progress: '已读81%', local: 1},
          {icon: '/static/good_book6.png', name: '小王子', progress: '已读76%', local: 1},
        ]
      }
    },
    methods: {
      filter (i) {
        this.seleted = i
      }
    },
    computed: {
      bookNum () {
        return this.items.length - 1
      }
    },
  }
</script>

<style scoped>
  /*容器*/
  .container {
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /*标题*/
  .title {
    width: 100%;
    height: 50px;
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px #c0c0c0 solid;
    font-size: 1em;
    color: #74bee5;
  }

  .title div {
    width: 3px;
    height: 20px;
    background-color: #74bee5;
    margin: 0 15px 0 20px;
  }

  .title img {
    max-width: 30px;
    max-height: 30px;
    margin-right: 15px;
  }

  .tab {
    width: 70%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px 0;
  }

  .tab-item {
    flex: 1;
    font-size: 15px;
  }

  .tab_selected {
    color: #74bee5;
  }

  /*书架*/
  .book-shelter {
    width: 100%;
    overflow: scroll;
  }

  .book-shelter .item {
    /*这个竟然可以！！！*/
    float: left;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5px;
    width: 33%;
  }

  .book-shelter .item img {
    max-width: 80px;
    max-height: 90px;
  }

  .bookName {
    margin-top: 5px;
    font-size: 13px;
  }

  .progress {
    font-size: 12px;
    color: rgb(179, 174, 184);
  }


</style>



