<template>
  <div class="container">
    <!--标题-->
    <div class="title">
      <div></div>
      我的
      <span style="flex: 1"></span>
      <img src="/static/fragment_title_setting.png">
    </div>

    <!--很蠢的挡住-->
    <div style="height: 50px"></div>

    <!--头部-->
    <div class="head">
      <div>
        <img class="icon" src="/static/user_icon.jpg">
        <span style="flex: 1;"></span>
        <span>xiaoLiu</span>
        <img class="icon" src="/static/user_right.png" @click="toUserInfo">
      </div>
    </div>

    <!--中间-->
    <div class="mid">
      <div class="item">
        <span style="font-size: 15px;color: #bdbdbe">阅读本数</span>
        <span style="font-size: 14px;color: #ebebea;margin-top: 10px">5本</span>
      </div>
      <div style="width: 1px;height: 60px;background-color: #d6d6d6"></div>
      <div class="item">
        <span style="font-size: 15px;color: #bdbdbe">阅读字数</span>
        <span style="font-size: 14px;color: #ebebea;margin-top: 10px">56.2W字</span>
      </div>
      <div style="width: 1px;height: 60px;background-color: #d6d6d6"></div>
      <div class="item">
        <span style="font-size: 15px;color: #bdbdbe">阅读时间</span>
        <span style="font-size: 14px;color: #ebebea;margin-top: 10px">52.3小时</span>
      </div>
    </div>

    <!--行-->
    <div class="row">
      <img class="row-left" src="/static/chengzhangzhilv.png">
      <span class="row-middle">成长之旅</span>
      <div style="flex: 1"></div>
      <img class="row-right" src="/static/boy.png" style="max-width: 70px;height: 90%">
    </div>
    <div class="row" @click="toReadPlan">
      <img class="row-left" src="/static/yuedujihua.png">
      <span class="row-middle">阅读计划</span>
      <div style="flex: 1"></div>
      <span class="row-right">今天的阅读计划完成了吗？</span>
    </div>
    <div class="row">
      <img class="row-left" src="/static/jiazhangguanzhu.png">
      <span class="row-middle">家长关注</span>
      <div style="flex: 1"></div>
      <span class="row-right">随时了解孩子的阅读</span>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'Me',
    methods: {
      toUserInfo () {
        this.$router.push('/userinfo')
      },
      toReadPlan () {
        this.$router.push('/readplan')
      }
    }
  }
</script>

<style scoped>
  /*容器*/
  .container {
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }

  /*标题*/
  .title {
    width: 100%;
    height: 50px;
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px #c0c0c0 solid;
    font-size: 1em;
    color: #74bee5;
  }

  .title div {
    width: 3px;
    height: 20px;
    background-color: #74bee5;
    margin: 0 15px 0 20px;
  }

  .title img {
    max-width: 30px;
    max-height: 30px;
    margin-right: 15px;
  }

  /*头部*/
  .head {
    width: 100%;
    height: 170px;
    background-image: url("/static/user_bg.png");
    background-repeat: no-repeat;
    background-size: 100% 170px;
    position: relative;
  }

  .head div {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 80px;
    margin-top: 30px;
  }

  .head .icon {
    max-width: 80px;
    max-height: 80px;
    border-radius: 50%;
    margin: 0 40px;
  }

  .head span {
    color: white;
  }

  /*中间*/
  .mid {
    width: 100%;
    height: 60px;
    border-bottom: 1px #d6d6d6 solid;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .mid .item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  /*一行*/
  .row {
    width: 100%;
    height: 80px;
    border-bottom: 1px #d6d6d6 solid;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .row .row-left {
    margin-left: 20px;
    width: 30px;
    height: 30px;
  }

  .row .row-middle {
    margin-left: 20px;
    font-size: 1em;
  }

  .row .row-right {
    margin-right: 20px;
    font-size: 13px;
    color: #d6d6d6;
  }

  .row:active {
    background-color: #f4f4f4;
  }

</style>



