<template>
  <div class="container">

    <!--类似fragment-->
    <div style="width: 100%;flex: 1;overflow: auto;">
      <router-view/>
    </div>

    <!--底部导航栏-->
    <div class="bottom-nav">
      <ul>
        <router-link tag="div" class="li" v-for="(item,index) in items" @click.native="setTab(index)" :to="item.url">
          <img :src="item.title === selcted_title ? item.selected_img : item.unselect_img">
          <div :class="{text_selected: selcted_title === item.title}">{{ item.title }}</div>
        </router-link>
      </ul>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'Index',
    data () {
      return {
        selcted_title: '首页',
        items: [
          {
            title: '首页',
            unselect_img: '/static/home_unselected.png',
            selected_img: '/static/home_selected.png',
            url: '/index/home'
          },
          {
            title: '活动',
            unselect_img: '/static/activity_unselected.png',
            selected_img: '/static/activity_selected.png',
            url: '/index/activity'
          },
          {
            title: '阅读',
            unselect_img: '/static/read_unselected.png',
            selected_img: '/static/read_selected.png',
            url: '/index/read'
          },
          {
            title: '作业',
            unselect_img: '/static/homework_unselected.png',
            selected_img: '/static/homework_selected.png',
            url: '/index/homework'
          },
          {
            title: '我的',
            unselect_img: '/static/me_unselected.png',
            selected_img: '/static/me_selected.png',
            url: '/index/me'
          },
        ],
      }
    },
    mounted () {
      const url = (window.location.href).split('/')
      const select = url[url.length - 1]
      if (select === 'home') {
        this.selcted_title = '首页'
      } else if (select === 'activity') {
        this.selcted_title = '活动'
      } else if (select === '阅读') {
        this.selcted_title = 'read'
      } else if (select === 'homework') {
        this.selcted_title = '作业'
      } else if (select === 'me') {
        this.selcted_title = '我的'
      }
    },
    methods: {
      setTab: function (index) {
        this.selcted_title = this.items[index].title
      }
    }
  }
</script>

<style scoped>
  /*容器*/
  .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /*底部导航栏*/
  .bottom-nav {
    width: 100%;
    height: 8%;
    background-color: white;
    border-top: #9b9b9f 1px solid;
  }

  .bottom-nav ul {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  /*底部每一个*/
  .bottom-nav .li {
    text-align: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style: none;
    font-size: 12px;
    color: #c3c3c5;
  }

  .bottom-nav img {
    height: 30%;
    width: 30%;
    margin: 2px 0;
  }

  /*文字选中*/
  .bottom-nav .text_selected {
    color: #6bcbcc;
  }
</style>



