<template>
  <div class="radiogroup">
    <div class="radio" @click="changeSelect(0)">
      <span class="check">{{checkMan}}</span>
      <span>男</span>
    </div>
    <div class="radio" @click="changeSelect(1)">
      <span class="check">{{checkWomen}}</span>
      <span>女</span>
    </div>

  </div>
</template>

<script>
  export default {
    data(){
      return{
        selected:0,
        checkMan:'√',
        checkWomen:''
      }
    },
    mounted(){
      switch (this.selected) {
        case 0:
          this.checkMan="√";
          this.checkWomen="";
          break;
        case 1:
          this.checkMan="";
          this.checkWomen="√";
          break;
        default:
          break;
      }
    },
    methods:{
      changeSelect(i){
        this.selected=i;
        //值改变后，返回数据
        this.$emit("changeSelect",this.selected);
        switch (i) {
          case 0:
            this.checkMan="√";
            this.checkWomen="";
            break;
          case 1:
            this.checkMan="";
            this.checkWomen="√";
            break;
          default:
            break;
        }
      }
    }
  }

</script>

<style scoped>
  .radiogroup{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .radio{
    flex:1;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px 0;
    color: white;
  }
  .check{
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 2px solid white;
    margin-right: 10px;
  }


</style>



