import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Index from '@/components/Index'
import Home from '@/components/Home'
import Activity from '@/components/Activity'
import Read from '@/components/Read'
import HomeWork from '@/components/HomeWork'
import Me from '@/components/Me'
import UserInfo from '@/components/UserInfo'
import AllBook from '@/components/AllBook'
import ReadPlan from '@/components/ReadPlan'
import BookDetail from '@/components/BookDetail'
import AllActivity from '@/components/AllActivity'
import Register from '@/components/Register'
import BookPostil from '@/components/BookPostil'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      redirect: '/index/home',
      name: 'Index',
      component: Index,
      children:[
        {path:'home', name:'Home', component:Home,meta:{keepAlive:true}},
        {path:'activity', name:'Activity', component:Activity,meta:{keepAlive:true}},
        {path:'read', name:'Read', component:Read,meta:{keepAlive:true}},
        {path:'homework', name:'HomeWork', component:HomeWork,meta:{keepAlive:true}},
        {path:'me', name:'Me', component:Me,meta:{keepAlive:true}},
      ],
      meta:{keepAlive:true},
    },
    {
      path: '/userInfo',
      name: 'UserInfo',
      component:UserInfo
    },
    {
      path: '/allbook',
      name: 'AllBook',
      component: AllBook
    },
    {
      path: '/readplan',
      name: 'ReadPlan',
      component: ReadPlan
    },
    {
      path: '/bookdetail',
      name: 'BookDetail',
      component: BookDetail
    },
    {
      path: '/allactivity',
      name: 'AllActivity',
      component: AllActivity
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/bookpostil',
      name: 'BookPostil',
      component: BookPostil
    }
  ]
})



