<template>
  <div class="container">
    <my-title title="我的批注" rightImg="/static/postil_scan.png"></my-title>
    <!--筛选框-->
    <div class="filter">
      <div class="filter-down" @click="downToogle">{{selectedType}}</div>
      <div style="flex: 1;margin-right: 15px;display: flex;background: white;border-radius: 5px;padding: 5px 10px;">
        <input style="flex: 1;border: none;" placeholder="请输入需要搜索的批注关键字"/>
        <img src="/static/activity_find.png">
      </div>
    </div>
    <!---->
    <div class="body">

      <!--蒙砂-->
      <div class="mask" @click="downToogle">
        <!--下拉框-->
        <div class="down-box">
          <div class="class-item"
               v-for="(item,index) in types"
               @click.stop="selectClass(index)">
            <div style="margin-left: 10px;">{{item.typeName}}</div>
            <div style="flex: 1;"></div>
            <div style="margin-right: 10px;">√</div>
          </div>
        </div>
      </div>
      <!--活动列表-->

      <div class="bookList" v-for="(postil,index) in postils"
           v-if="selectedType=='全部批注' || types[postil.type-1].typeName==selectedType">
        <!--左边图片-->
        <img class="left" :src="types[postil.type-1].icon">

        <!--中间内容-->
        <div class="mid">
          <div style="font-size: 0.8em;color: #222">{{postil.content}}</div>
          <div style="font-size: 0.7em;margin-top: 10px;color: #a4a4a4">
            <span>{{types[postil.type-1].typeName}}</span>
            <span style="margin-left: 20px;">{{postil.time}}</span>
          </div>
        </div>

      </div>

    </div>

  </div>
</template>

<script>
  import MyTitle from '@/components/common/MyTitle'

  export default {
    name: 'BookPostil',
    components: {MyTitle},
    data () {
      return {
        selectedType: '全部批注',
        postils: [
          {content: '巴学园这种教学方式对于少部分有钱人可以。推广到整个教育都用巴学园的方式能行...', type: 5, time: '2018-08-02'},
          {content: '这段话应用了拟人的手法，"头发在迎风唱歌"，表现了孩子们此时愉快的心情。', type: 3, time: '2018-08-01'},
          {content: '"巴学园"有着与众不同的教育方式，每一天的第一节课，老师就把当天要上的课和每天...', type: 4, time: '2018-07-25'},
          {content: '眼花缭乱', type: 2, time: '2018-07-25'},
        ],
        types: [
          {typeName: '全部批注', icon: ''},
          {typeName: '符号批注', icon: '/static/postil2.png'},
          {typeName: '赏析批注', icon: '/static/postil3.png'},
          {typeName: '感想批注', icon: '/static/postil4.png'},
          {typeName: '疑问批注', icon: '/static/postil5.png'},
          {typeName: '自定义批注', icon: '/static/postil6.png'}
        ]
      }
    },
    mounted () {
      $('.down-box').slideToggle(0)
      $('.mask').fadeToggle(0)
    },
    methods: {
      downToogle () {
        $('.down-box').slideToggle(300)
        $('.mask').fadeToggle(300)
      },
      selectClass (i) {
        this.selectedType = this.types[i].typeName
        this.downToogle()
      },
      toBookDetail (i) {
        this.$router.push('/bookdetail')
      }
    }
  }
</script>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
    background-color: #f4f4f4;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }

  .body {
    width: 100%;
    height: 100%;
    overflow: auto;
  }

  .mask {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(217, 217, 217, 0.41);
  }

  .down-box {
    width: 100%;
    background-color: #f1f9fc;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 10px 0;
  }

  .down-box .class-item {
    padding: 10px 5px;
    color: #3e83a9;
    font-size: 0.9em;
    text-align: left;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .filter {
    width: 100%;
    background-color: #bbe0f2;
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .filter-down, .filter-read, .filter-postil {
    font-size: 0.9em;
    color: white;
    margin: 0 15px;
  }

  .bookList {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid #dddddd;
    background: white;
  }

  .left {
    max-width: 50px;
    max-height: 60px;
    margin: 15px;
  }

  .mid {
    flex: 1;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin: 0 10px;
  }

  .right {
    margin: 10px;
    background-color: #69aae6;
    border: none;
    color: white;
    padding: 5px 15px;
    height: 30px;
    border-radius: 15px;
  }

  .right:focus {
    outline: none;
  }

  .right:active {
    background-color: #79c0e6;
  }

</style>



