<template>

  <div class="container">

    <!--标题-->
    <div class="title">
      <div></div>
      阅读作业
    </div>

    <!--很蠢的挡住-->
    <div style="height: 50px"></div>

    <mt-navbar style="width: 80%" v-model="selected">
      <mt-tab-item id="0" class="tab">班级</mt-tab-item>
      <mt-tab-item id="1" class="tab">作业</mt-tab-item>
    </mt-navbar>

    <!-- tab-container -->
    <mt-tab-container style="width: 100%;margin-top: 10px;" v-model="selected">

      <!--班级界面-->
      <mt-tab-container-item id="0">
        <div style="display: flex;flex-direction: column;align-items: center;">
          <!--头部-->
          <div class="class-head">
            <div style="margin: 0 15px;font-size: 1.1em;color: #3d85a9">
              <span>浙理三班</span>
              <span style="margin-left: 10px">班级成员：3人</span>
            </div>
            <div style="margin: 2px 15px;font-size: 0.9em;color: #5893b3;">这是一个热爱阅读的班级</div>
          </div>
          <div
            style="width: 90%;text-align: left;display: flex;flex-direction: row;align-items: center;margin-top: 20px;color: #77c0e6;font-size: 0.9em">
            <div style="width: 2px;height: 18px;background-color: #77c0e6;margin-right: 20px;"></div>
            同学名单
            <div style="flex: 1"></div>
            <div style="color: #D0D0D0 ">负责老师：陈老师</div>
          </div>
          <!--学生列表-->
          <div class="student-list" v-for="student in students">
            <img src="/static/user_icon.jpg">
            <div style="margin-left: 10px;color: #9c9b9f;">{{student.name}}</div>
            <div style="flex: 1;"></div>
            <div style="color: #9c9ba0;">最近在读</div>
            <div style="color: #1bbb9b">《{{student.book}}》</div>
          </div>
        </div>
      </mt-tab-container-item>

      <!--作业界面-->
      <mt-tab-container-item id="1">
        <div style="display: flex;flex-direction: column;align-items: center;">
          <div class="homework" v-for="homework in homeworks">
            <div>
              <span style="font-size: 1em;color: #4085a8">{{homework.name}}</span>
              <span style="font-size: 1em;" :style="{color:homework.state_color1}">[{{homework.state1}}]</span>
            </div>
            <div>
              <span style="font-size: 0.9em;color: #878c91">《{{homework.bookName}}》</span>
              <span style="font-size: 0.9em;" :style="{color:homework.state_color2}">{{homework.state2}}</span>
            </div>
          </div>
        </div>
      </mt-tab-container-item>

    </mt-tab-container>

  </div>
</template>

<script>
  export default {
    name: 'HomeWork',
    data () {
      return {
        selected: 0,
        students: [
          {icon: '', name: 'xiaoWu', book: '窗边的小豆豆'},
          {icon: '', name: 'xiaoCheng', book: '红楼梦'},
          {icon: '', name: 'xiaoZhou', book: '丁丁历险记'},
        ],
        homeworks: [
          {
            name: '撰写读后感',
            state1: '未提交',
            state_color1: 'red',
            bookName: '窗边的小豆豆',
            state2: '距离结束还有3天',
            state_color2: '#878c91'
          },
          {
            name: '谈谈你最佩服书中的哪个人物',
            state1: '已提交',
            state_color1: '#27a105',
            bookName: '爱的教育',
            state2: '教师未批改',
            state_color2: 'red'
          },
          {
            name: '用不多于200字简述这个故事',
            state1: '未通过',
            state_color1: 'red',
            bookName: '夏洛的网',
            state2: '教师已批改',
            state_color2: '#27a105'
          },
          {
            name: '摘录10处好句好段并简要分析',
            state1: '已通过',
            state_color1: '#27a105',
            bookName: '雾都孤儿',
            state2: '教师已批改',
            state_color2: '#27a105'
          },
        ]
      }
    },
    mounted () {
      const tabs = document.getElementsByClassName('mint-tab-item tab')
      console.log(tabs.length)
      tabs[0].click();
    }
  }
</script>

<style scoped>
  /*容器*/
  .container {
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }

  /*标题*/
  .title {
    width: 100%;
    height: 50px;
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px #c0c0c0 solid;
    font-size: 1em;
    color: #74bee5;
  }

  .title div {
    width: 3px;
    height: 20px;
    background-color: #74bee5;
    margin: 0 15px 0 20px;
  }

  .title img {
    max-width: 30px;
    max-height: 30px;
    margin-right: 15px;
  }

  /*选项卡*/
  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 1px solid #26a2ff;
  }

  .tab {
    margin: 0 50px;
  }

  /*班级界面*/
  .class-head {
    width: 90%;
    padding: 10px 0;
    border-radius: 5px;
    background-color: #f1f9fc;
    text-align: left;
  }

  .student-list {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 90%;
    margin-top: 20px;
  }

  .student-list img {
    width: 30px;
    height: 30px;
  }

  /*作业界面*/
  .homework {
    width: 90%;
    padding: 15px 0;
    margin: 5px 0;
    background: url("/static/my_home_work_bg.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .homework div {
    width: 85%;
    text-align: left;
    margin: 3px 0;
  }


</style>



