<template>
  <div class="title">
    <img class="left" src="/static/left_unpressed.png" @click="back">
    <div>{{title}}</div>
    <img class="right" :src="rightImg" @click="titleRightImgClick">
  </div>
</template>

<script>
  export default {
    props:["title","rightImg"],
    methods:{
      back(){
        this.$router.back(-1);
      },
      titleRightImgClick(){
        this.$parent.titleRightImgClick();
      }
    }
  }
</script>

<style scoped>
  .title{
    width: 100%;
    height: 50px;
    background-color: #79c0e6;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .title .left{
    max-width: 20px;
    max-height: 20px;
    min-width: 12px;
    margin-left: 20px;
  }
  .title .right{
    max-width: 20px;
    max-height: 20px;
    min-width: 12px;
    margin-right: 20px;
  }
  .title div{
    flex: 1;
    color: white;
    font-size: 1.1em;
  }

</style>



