<template>
  <div class="container">
    <my-title :title="name" rightImg="/static/share.png"></my-title>
    <!--头部-->
    <div class="head">
      <div style="width: 90%;height: 100px;background-color: white;border-radius: 8px;
      position: absolute;bottom: 0;left: 0;right: 0;margin: 0 auto;">
        <!--加入书架-->
        <button>加入书架</button>
        <!--简介-->
        <div style="height: 55px;overflow: hidden">
          <div style="text-align: left;font-size: 13px;margin: 5px 15px;">{{bookDescribe}}</div>
        </div>

        <!--书本-->
        <img :src="icon"
             style="width: 60px;position: absolute;left: 10%;top: -45%;">
      </div>
    </div>

    <div class="body">

      <!--中间-->
      <div class="mid">
        <div class="function" v-for="(func,index) in functions" @click="funcClick(index)">
          <img style="width: 40%;" :src="func.icon">
          <span style="margin-top: 2px;font-size: 13px;">{{func.name}}</span>
        </div>
      </div>
      <!--精彩批注-->
      <div class="bottom">
        <!--上边-->
        <div style="width: 100%;display: flex;flex-direction: row;align-items: center;">
          <div style="height: 15px;width: 2px;background-color: grey;margin: 0 10px;"></div>
          <div style="font-size: 14px">精彩批注</div>
          <div style="flex: 1;"></div>
          <div style="font-size: 12px;color:#1ca9ff;">如何成为精彩批注?</div>
        </div>

        <!--精彩批注表-->
        <div style="width: 100%;display: flex;flex-direction: row;margin: 7px;border-bottom: 1px solid grey"
             v-for="postil in postils">
          <img style="border-radius: 50%;width: 40px;height: 40px;margin-left: 7px;" src="/static/user_icon.jpg">
          <div style="flex: 1;margin: 0 5px;display: flex;flex-direction: column;margin-top: 10px;">
            <div style="font-size: 13px;text-align: left;">{{postil.name}}</div>
            <div style="font-size: 13px;text-align: left;margin-top: 2px;color: #b0b0b0">{{postil.content}}</div>
            <div style="font-size: 13px;text-align: left;margin-top: 10px;margin-bottom: 5px;color: #222">{{postil.time}}</div>
          </div>
        </div>

        <!--底部-->
        <div style="font-size: 13px;">
          查看全部
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import MyTitle from '@/components/common/MyTitle'
  export default {
    name: 'BookDetail',
    components:{MyTitle},
    data(){
      return{
        name:"窗边的小豆豆",
        icon:"/static/good_book1.png",
        bookDescribe:"《窗边的小豆豆》是日本作家、主持人黑柳彻字创建的一个儿童文学作品，首次出版于1981年。这本书讲述了作者小时候的一段真实故事",
        functions:[
          {icon:"/static/detail_read.png",name:"阅读"},
          {icon:"/static/detail_postil.png",name:"批注"},
          {icon:"/static/detail_note.png",name:"小报"},
          {icon:"/static/detail_activity.png",name:"活动"},
        ],
        postils:[
          {id:"6",type:"",number:"",name:"xiaoZhou",book:"",content:"'巴学园'有着与众不同的教育方法，每天的第一节课，老师就把当天要上的课和每一节的学习重点，都写在黑板上，然后说：'下面开始上课了，从你喜欢的那门课开始吧。'，慢慢老师就会知道每一个同学的兴趣所在，和他感兴趣的方式。",teacher_remark:"",book_location:"",time:"2019-06-07"},
          {id:"7",type:"",number:"",name:"xiaoChen",book:"",content:"这个叫小豆豆的女孩眼里所展现的童真世界是我们曾经忽略和淡忘的美好事物，这种忽略通常是一生的，那些生命中非常宝贵的东西",teacher_remark:"",book_location:"",time:"2019-06-08"},
          {id:"8",type:"",number:"",name:"xiaoZhou",book:"",content:"这段话应用了拟人的手法，\"头发在迎风唱歌\"，表现了孩子们此时愉快的心情。",teacher_remark:"",book_location:"",time:"2019-06-09"},
        ]
      }
    },
    methods: {
      funcClick (i) {
        switch (i) {
          case 1:
            this.$router.push("/bookpostil");
            break;
          default:
            this.$toast(this.functions[i].name);
            break;
        }
      }
    }
  }
</script>

<style scoped>
  .container{
    width: 100%;;
    background-color: #f4f4f4;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }
  .body{
    flex: 1;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
  }
  .head{
    width: 100%;
    height: 150px;
    position: relative;
    background: url("/static/book_detail_bg.png");
    background-size: 100% 70%;
    background-repeat: no-repeat;
  }
  .head button{
    border: none;
    background-color: #7ac2ff;
    color: white;
    font-size: 14px;
    padding: 4px 20px;
    margin-top: 10px;
    border-radius: 15px;
  }
  .mid{
    width: 100%;
    background-color: white;
    padding: 8px 0;
    margin: 15px 0;
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .mid .function{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .bottom{
    width: 95%;
    padding: 2.5%;
    background-color: white;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>



