<template>
  <!--整体容器-->
  <div class="container">
    <!--内容-->
    <div class="content">

      <!--账号-->
      <div class="row">
        <img src="/static/login_num.png">
        <input v-model="number" placeholder="请输入账号" type="number">
      </div>

      <!--密码-->
      <div class="row">
        <img src="/static/login_pwd.png">
        <input v-model="password" placeholder="请输入密码" type="password">
      </div>

      <!--忘记密码-->
      <div class="row">
        <div style="width: 100%; text-align: right;font-size: 13px;color: #c8c8c9">忘记密码？</div>
      </div>

      <!--两个按钮-->
      <div class="row">
        <button style="background-color: white;color: #79c0e6" @click="register()">注册</button>
        <button style="background-color: #79c0e6;color: white" @click="login()">登录</button>
      </div>

    </div>

  </div>
</template>

<script>
  export default {
    name: 'Login',
    data () {
      return {
        number: '',
        password: ''
      }
    },
    methods: {
      login: function () {
        //定义一个_this指向全局
        if (this.number === '22051120' && this.password === '123456') {
          this.$router.push('/index')
        } else {
          this.$toast('账号或密码错误')
        }
      },
      register: function () {
        this.$router.push('/register')
      }
    }
  }
</script>

<style scoped>
  /*容器*/
  .container {
    height: 100%;
    width: 100%;
    background: url("/static/login_bg.png") no-repeat;
    background-size: 100% 100%;
  }

  /*内容*/
  .content {

    width: 70%;
    min-height: 100px;
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /*用户名*/
  .row {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px 0;
  }

  /**/
  .row img {
    width: 32px;
    height: 32px;
    margin-right: 20px;
  }

  .row input {
    flex: 1;
    height: 30px;
    text-align: center;
    border-radius: 5px;
    background-color: #bbe0f2;
    border: none;
    color: #fafcfc;
  }

  .row input::placeholder {
    color: #fafcfc;
  }

  .row button {

    flex: 1;
    margin: 0 10px;
    padding: 7px 0;
    border-radius: 5px;
    border: 1px solid #79c0e6;
  }
</style>



