<template>

  <div>
    <!--标题-->
    <div class="title">
      <div></div>
      阅读活动
    </div>

    <!--整体容器-->
    <div class="container">

      <!--很蠢的挡住-->
      <div style="height: 50px"></div>

      <!--官方活动-->
      <div class="offical-activity">
        <img src="/static/official_activity.png">
      </div>

      <!--两个按钮-->
      <div class="double-button">
        <a @click="toAllActivity"><img src="/static/activity_all_activity.png">全部<br/>活动</a>
        <div style="width: 2px;height: 35px;background-color: #75bfe3"></div>
        <a><img src="/static/activity_my_activity.png">我的<br/>活动</a>
      </div>

      <!--小标题-->
      <div class="litter-title">
        <div></div>
        精彩活动
      </div>

      <!--活动推荐-->
      <ul class="nice-activity" id="activity">
        <li class="item" v-for="item in items">
          <img :src="item.icon">
          <div>
            <p style="color: #96979a">{{item.title}}...</p>
            <p style="color: #e3e3e3">《{{item.book}}》</p>
            <span>已有{{item.good}}人参加</span>
          </div>
        </li>
      </ul>

    </div>
  </div>

</template>

<script>
  export default {
    name: 'Activity',
    data(){
      return{
        items:[
          {icon:"/static/good_book1.png",title:"假如我在巴学园上学",book:"窗边的小豆豆",good:"240"},
          {icon:"/static/good_book3.png",title:"为你编织童年的网",book:"夏洛的网",good:"407"},
          {icon:"/static/good_book2.png",title:"如何写好成长日记",book:"爱的教育",good:"1207"},
          {icon:"/static/good_book1.png",title:"假如我在巴学园上学",book:"窗边的小豆豆",good:"240"},
          {icon:"/static/good_book3.png",title:"为你编织童年的网",book:"夏洛的网",good:"407"},
          {icon:"/static/good_book2.png",title:"如何写好成长日记",book:"爱的教育",good:"1207"},
        ],
      }
    },
    methods:{
      toAllActivity(){
        this.$router.push("/allactivity")
      }
    }
  }
</script>

<style scoped>
  /*容器*/
  .container{
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }
  /*标题*/
  .title{
    width: 100%;
    height: 50px;
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px #c0c0c0 solid;
    font-size: 1em;
    color: #74bee5;
  }
  .title div{
    width: 3px;
    height: 20px;
    background-color: #74bee5;
    margin: 0 15px 0 20px;
  }
  /*官方活动*/
  .offical-activity{
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  .offical-activity img{
    width: 90%;
    height: 200px;
  }
  /*两个按钮*/
  .double-button{
    width: 90%;
    height: 60px;
    background-color: #f0fafb;
    border-radius: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .double-button a{
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    color: #79c1e5;
  }
  .double-button a img{
    width: 35px;
    height: 35px;
    margin-right: 10px;
  }
  /*小标题*/
  .litter-title{
    font-size: 13px;
    color: #80c5e4;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    width: 90%;
  }
  .litter-title div{
    margin-right: 5px;
    width: 3px;
    height: 15px;
    background-color: #80c5e4;
    border-radius: 5px;
  }
  /*精彩活动*/
  .nice-activity{
    width: 100%;
    margin: 5px 0;
  }
  p{
    text-align: left;
  }
  .nice-activity .item{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 5px 5%;
  }
  .nice-activity .item:active{
    background-color: #f4f4f4;
  }
  .nice-activity .item img{
    width: 50px;
    height: 65px;
  }
  .nice-activity .item div{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: right;
    margin: 5px 10px;
  }
  .nice-activity .item p{
    font-size: 14px;
  }
  .nice-activity .item span{
    text-align: right;
    font-size: 11px;
    color: #3d86a8;
  }
</style>



