<template>
  <div class="container">
    <my-title title="个人信息" right-img="/static/save.png"></my-title>
    <div class="bg">
      <div style="width: 100%;height: 200px;position:relative;">
        <img class="userIcon" src="/static/user_icon.jpg">
      </div>
      <div class="row">
        <span>用户名</span>
        <span style="flex: 1"></span>
        <span style="width: 70%;">{{number}}</span>
      </div>
      <div class="row">
        <span>姓名</span>
        <span style="flex: 1"></span>
        <input placeholder="请输入姓名" v-model="name">
      </div>
      <div class="row">
        <span>新密码</span>
        <span style="flex: 1"></span>
        <input placeholder="请输入密码" type="password" v-model="userPwd">
      </div>
      <div class="row">
        <span>重复密码</span>
        <span style="flex: 1"></span>
        <input placeholder="请重复密码" type="password" v-model="userPwdRep">
      </div>
      <div class="row">
        <span>出生年月</span>
        <span style="flex: 1"></span>
        <input placeholder="请输入出生年月" v-model="birthdate">
      </div>
      <div class="row">
        <span>性别</span>
        <span style="flex: 1"></span>
        <sex-radio style="width: 60%;" @changeSelect="changeSex"></sex-radio>
      </div>
    </div>
  </div>

</template>

<script>
  import MyTitle from '@/components/common/MyTitle'
  import SexRadio from '@/components/common/SexRadio'

  //当前时间
  Date.prototype.format = function (format) {
    var args = {
      'M+': this.getMonth() + 1,
      'd+': this.getDate(),
      'h+': this.getHours(),
      'm+': this.getMinutes(),
      's+': this.getSeconds(),
      'q+': Math.floor((this.getMonth() + 3) / 3),  //quarter
      'S': this.getMilliseconds()
    }
    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (var i in args) {
      var n = args[i]
      if (new RegExp('(' + i + ')').test(format)) {
        format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? n : ('00' + n).substr(('' + n).length))
      }
    }
    return format
  }

  export default {
    name: 'UserInfo',
    components: {MyTitle, SexRadio},
    data () {
      return {
        number: '0001',
        name: 'xiaoZhou',
        userPwd: '',
        userPwdRep: '',
        birthdate: '2004-06-20',
        sex: 0
      }
    },
    methods: {
      changeSex (i) {
        this.sex = i
      },
      titleRightImgClick () {

        if (this.userPwd != this.userPwdRep) {
          _this.$toast('两次密码不一致！')
          return
        }

        var _this = this
        $.ajax({
          url: _this.GLOBAL.updateUser,
          data: {'number': _this.number, 'password': _this.userPwd, 'birthdate': _this.birthdate, 'sex': _this.sex},
          success: function () {
            _this.$toast('修改成功！')
          }
        })
        //
      }
    }
  }
</script>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }

  .bg {
    width: 100%;
    height: 100%;
    background: url("/static/user_info_bg.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .userIcon {
    border-radius: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100px;
    height: 100px;
    border: 1px solid white;
  }

  .row {
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 0;
  }

  .row input {
    width: 70%;
    padding: 8px 0;
    background-color: white;
    border: none;
    border-radius: 10px;
    font-size: 0.8em;
    text-align: center;
  }

  .row input:focus {
    outline: none;
  }
</style>



