<template>
  <div class="container">
    <my-title title="阅读计划"></my-title>
    <div class="body">
      <!--计划列表-->
      <div class="planList">
        <!--每一项-->
        <div class="item" v-for="(plan,index) in plans" @click="toPlanDetail(index)">
          <!--背景图-->
          <img class="bg" src="/static/item_read_plan.png">
          <!--居中-->
          <div class="center" v-show="index!=0">
            <div style="font-size: 14px;color: #a5a7aa">《{{plan.book}}》</div>
            <div style="font-size: 13px;margin-top: 5%;color: #a5a7aa">完成度为{{plan.progress}}</div>
          </div>
          <!--添加书籍-->
          <img style="position: absolute;left: 0;top: 0;right: 0;bottom: 0;margin: auto;width: 30%;"
               src="/static/read_plan_add.png" v-show="index==0">
          <!--已完成-->
          <img class="state" src="/static/read_plan_complete.png" v-show="plan.state==1">
          <!--未完成-->
          <img class="state" src="/static/read_plan_uncomplete.png" v-show="plan.state==2">
          <!--家长定制-->
          <img class="parent" src="/static/parent_plan.png" v-show="plan.is_parent==1">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import MyTitle from '@/components/common/MyTitle'

  export default {
    name: 'ReadPlan',
    components: {MyTitle},
    data () {
      return {
        //状态0正在进行；1已经完成；2未完成
        plans: [
          {id: '', number: '', book: '', start_time: '', end_time: '', progress: '', state: '', is_parent: ''},
          {
            id: '1',
            number: '2016329621104',
            book: '窗边的小豆豆',
            start_time: '',
            end_time: '',
            progress: '40%',
            state: '0',
            is_parent: '0'
          },
          {
            id: '2',
            number: '2016329621104',
            book: '爱的教育',
            start_time: '',
            end_time: '',
            progress: '100%',
            state: '1',
            is_parent: '1'
          },
          {
            id: '3',
            number: '2016329621104',
            book: '雾都孤儿',
            start_time: '',
            end_time: '',
            progress: '90%',
            state: '2',
            is_parent: '0'
          },
          {
            id: '4',
            number: '2016329621104',
            book: '解忧杂货店',
            start_time: '',
            end_time: '',
            progress: '70%',
            state: '0',
            is_parent: '0'
          },
        ]
      }
    },
    methods: {
      toPlanDetail (i) {
        if (i == 0) {
          alert('添加书籍')
        } else {
          alert('阅读计划：' + this.plans[i].book)
        }
      }
    }
  }
</script>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }

  .body {
    width: 100%;
    height: 100%;
    overflow: auto;
    background: url("/static/read_plan_bg.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }

  .planList {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
  }

  .item {
    width: 50%;
    position: relative;
    margin: 15px 0;
  }

  .item .bg {
    width: 80%;
  }

  .item .center {
    left: 10%;
    right: 10%;
    top: 23%;
    position: absolute;
    display: block;
    transform: rotate(5deg);
  }

  .item .state {
    width: 60px;
    position: absolute;
    right: 20%;
    bottom: 10%;
  }

  .item .parent {
    position: absolute;
    left: 10%;
    width: 30%;
  }

</style>



