<template>
  <div>
    <!--顶部-->
    <div class="title">
      <img src="/static/logo.png">
      <!--中间输入-->
      <div class="input">
        <input placeholder="请输入需要搜索的书籍">
        <img class="search" src="/static/home_find.png">
      </div>
      <img src="/static/home_msg.png">
    </div>

    <!--整体容器-->
    <div class="container">

      <!--很蠢的挡住-->
      <div style="height: 50px"></div>

      <!--轮播图-->
      <mt-swipe style="width: 100%; height: 150px;" :auto="4000" :show-indicators="true">
        <mt-swipe-item v-for="page in pages">
          <img :src="page.img" style="width: 100%;height: 100%">
        </mt-swipe-item>
      </mt-swipe>

      <!--中间两个按钮-->
      <div class="double-button">
        <a @click="toBookList(0)">
          <img src="/static/home_all_book.png">
          <span>全部<br/>书籍</span>
        </a>
        <div style="width: 2px;height: 40px; background-color: #78C1E4"></div>
        <a @click="toBookList(1)">
          <img src="/static/home_recend_read.png">
          <span>最近<br/>在读</span>
        </a>
      </div>

      <!--小标题-->
      <div class="litter-title">
        <div></div>
        好书推荐
      </div>

      <!--中间四本书-->
      <div class="books">
        <div class="book">
          <img src="/static/good_book1.png">
          <span>窗边的小豆豆</span>
        </div>
        <div class="book">
          <img src="/static/good_book2.png">
          <span>爱的教育</span>
        </div>
        <div class="book">
          <img src="/static/good_book3.png">
          <span>夏洛的网</span>
        </div>
        <div class="book">
          <img src="/static/good_book4.png">
          <span>雾都孤儿</span>
        </div>
      </div>

      <!--小标题-->
      <div class="litter-title">
        <div></div>
        精彩活动
      </div>

      <!--活动推荐-->
      <ul class="nice-activity" id="activity">
        <li class="item" v-for="item in items">
          <img :src="item.icon">
          <div>
            <p style="color: #96979a">{{item.title}}...</p>
            <p style="color: #e3e3e3">《{{item.book}}》</p>
            <span>已有{{item.good}}人参加</span>
          </div>
        </li>
      </ul>

    </div>

  </div>

</template>

<script>
  export default {
    name: 'Home',
    data () {
      return {
        items: [
          {icon: '/static/good_book1.png', title: '假如我在巴学园上学', book: '窗边的小豆豆', good: '240'},
          {icon: '/static/good_book3.png', title: '为你编织童年的网', book: '夏洛的网', good: '407'},
          {icon: '/static/good_book2.png', title: '如何写好成长日记', book: '爱的教育', good: '1207'},
          {icon: '/static/good_book1.png', title: '假如我在巴学园上学', book: '窗边的小豆豆', good: '240'},
          {icon: '/static/good_book3.png', title: '为你编织童年的网', book: '夏洛的网', good: '407'},
          {icon: '/static/good_book2.png', title: '如何写好成长日记', book: '爱的教育', good: '1207'},
        ],
        pages: [
          {img: '/static/banner1.png'},
          {img: '/static/banner2.png'},
          {img: '/static/banner3.png'},
          {img: '/static/banner1.png'},
          {img: '/static/banner2.png'},
          {img: '/static/banner3.png'},
        ]
      }
    },
    methods: {
      toBookList (i) {
        if (i === 0) {
          this.$router.push('/allbook')
        } else {
          alert('暂无最近在读')
        }
      }
    }
  }
</script>

<style scoped>

  /*容器*/
  .container {
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
  }

  /*标题*/
  .title {
    width: 100%;
    height: 50px;
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
  }

  /*标题的logo*/
  .title img {
    margin: 0 20px;
    max-height: 25px;
  }

  .title .input {
    width: 60%;
    height: 30px;
    border-radius: 20px;
    border: none;
    background-color: #69a7d2;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  /*搜索框*/
  .title .input input {
    height: 100%;
    flex: 1;
    border: none;
    padding: 0 5px;
    background-color: #69a7d2;
    margin-left: 15px;
  }

  .title .input input:focus {
    outline: none;
  }

  input::-webkit-input-placeholder {
    color: white;
  }

  /*搜索框旁边的放大镜*/
  .title .input .search {
    width: 20px;
    height: 20px;
  }

  /*中间两个按钮*/
  .double-button {
    width: 100%;
    height: 65px;
    background-color: #f0fafb;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .double-button a {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }

  .double-button a img {
    width: 37px;
    height: 37px;
  }

  .double-button a span {
    font-size: 14px;
    margin-left: 10px;
    color: #78c1e4;
  }

  /*四本推荐的书籍*/
  .books {
    width: 95%;
    padding: 5px 0;
    background: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: scroll;
  }

  .books .book {
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .books .book span {
    margin-top: 2px;
    font-size: 10px;
    color: #b5b5b7;
  }

  .books .book img {
    width: 80%;
    height: 70%;
  }

  /*小标题*/
  .litter-title {
    width: 90%;
    font-size: 13px;
    color: #80c5e4;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .litter-title div {
    margin-right: 5px;
    width: 3px;
    height: 15px;
    background-color: #80c5e4;
    border-radius: 5px;
  }

  /*精彩活动*/
  .nice-activity {
    width: 100%;
    margin: 5px 0;
  }

  p {
    text-align: left;
  }

  .nice-activity .item {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 5px 5%;
  }

  .nice-activity .item:active {
    background-color: #f4f4f4;
  }

  .nice-activity .item img {
    width: 50px;
    height: 65px;
  }

  .nice-activity .item div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: right;
    margin: 5px 10px;
  }

  .nice-activity .item p {
    font-size: 14px;
  }

  .nice-activity .item span {
    text-align: right;
    font-size: 11px;
    color: #3d86a8;
  }
</style>



